.inner { 
  position: relative;
  height: 100vh; width: 100%;
  .first{ 
    position: absolute; top:50%; left: 50%; 
    transform: translate(-50%, -50%);
    text-align: center;
    width: fit-content; height: fit-content;
    display: flex; flex-direction: column; justify-content: center; align-self: center; transition: 2s; opacity: 1;
    p.up { 
      font-size: 16px; font-weight: 400; margin-bottom: -15px; letter-spacing: 0.07em;
      .jun {
        color: rgb(231, 51, 51);
      }
      .ji {
        color: rgb(231, 51, 51);
      }
    }
    h4 {
      font-size: 72px; font-weight: 700; letter-spacing: 0.1em;
    }
    p.down {
      font-size: 16px; font-weight: 300; letter-spacing: 0.2em;
    }
  }
  .second { 
    opacity: 0; transition: 2s;
    position: absolute; top:50%; left: 50%; 
    transform: translate(-50%, -50%);
    h4{
      font-size: 42px; font-weight: 400; text-align: center;
    }
  }
  &:hover {
    .first{opacity: 0;}
    .second{opacity: 1;}
  }
}





