header {
  width: 100%; background-color: #00ffff;
  position: fixed; z-index: 10;
  .inner {
    width: 1200px; height: 40px; margin: 0 auto;
    display: flex; justify-content: space-between;

    h1 {
      a {
        line-height: 40px; font-size: 25px;
      }
    }
    ul.gnb{
        display: flex;
      li {
        margin: 0 20px;
        line-height: 40px;
      }
      span { 
        line-height: 40px; font-size: 10px; padding-top: 1px;
        color: #d3d3d3;
      }
    }

  }

}