#aboutInbox {
  background-color: aquamarine;
  display: flex; justify-content: space-between; align-items: center;
  .imgBox {
    height: 80%; width: 340px;
    background-color: #777;
  }
  .textBox {
    height: 80%; width: 62%;
    background-color: #777;
    h1 {
      margin-bottom: 20px;
      span {
        font-weight: 400;
      }
    }
    p {
      margin-bottom: 20px;
    }
    p:last-child {
      margin-bottom: 0;
    }
  }
}