
.dot-4px {
  width: 4px; height: 4px; display: block;
  background-color: #7CC6CB;
  margin-right: 7px;
}

#stackOutbox {  
  outline: 1px solid red;
  .stackInbox{
    position: relative;
    p { font-size: 22px; }
    width: 100%; height: 160vh;
    #devTool, #desiTool {
      width: 100%; height: 80vh;
      .imgGroup{
        display: flex; 
        > div { 
          margin-right: 20px; display: flex; flex-direction: column; justify-content: end;
        }
        img { margin-bottom: 15px;}
        p { text-align: center; font-weight: 600;}
        
        div.js {
          margin-left: 20px;
          img{
            transform: scale(0.7);
            transform-origin:50% 100%;
          }
        }
      }

      .devText, .desiText{
        margin-top: 20px;
        p{ display: flex; align-items: baseline; font-size: 18px; margin-bottom: 15px;}
      }
    } // dev,desiToop end
    #devTool {
      position: absolute; top:10rem; left:0;
      width: fit-content;
    }
    #desiTool {
      width: fit-content;
      height: fit-content;
      position:absolute; bottom:10rem; right: 0;
    }
  }
}
